export default {
	data(){
		return{
			// 当前打卡记录id
			id:'',
			recoderobj:{},
			imageUrl:this.$common.imageUrl
		}
	},
	onLoad(option){
		this.id = option.id
		this._gettraindetail()
	},
	methods:{
		_gettraindetail(){
			let obj ={
				id:this.id
			}
			this.$common.requests('showXunLianYing',obj).then(res=>{
				console.log(res.data.result)
				this.recoderobj = res.data.result
			})
		}
	}
}
